<template>
    <div :class="`caption d-flex ${approved ? 'green' : 'orange'}--text`">
        <v-icon small :color="approved ? 'green' : 'orange'">{{`mdi-${approved ? 'check' : 'pause'}-circle-outline`}}</v-icon>
        {{val}}
    </div>
</template>
<script>
export default {
    name:'TableFieldPic',
    props: {
        item: {
            type: Object,
        },
        header: {
            type: Object
        }
    },
    computed:{ 
        val () {
            return this.$getNestedValue(this.item, this.header.value)
        },
        approved () {
            return this.val == 'approved'
        }
    }
}
</script>

<style lang="scss" scoped>
.caption {
    text-transform: capitalize;
}
</style>>
